<template>
	<v-card>
		<v-card-title class="py-2">
			Edit Your Profil Startup
		</v-card-title>
		<v-form v-model="valid" ref="form">
			<v-card-text>
				<v-text-field
          v-model="dataStartup.name"
          label="Name"
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field> 
        <v-text-field
          v-model="dataStartup.profile.lokasi"
          label="Location"
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field>
        <v-text-field
          v-model="dataStartup.profile.phone"
          label="Phone"
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field>
        <v-text-field
          v-model="dataStartup.email"
          label="E-mail"
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field> 
        <v-text-field
          v-model="dataStartup.profile.website"
          label="Website"
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field>  
        <v-text-field
          v-model="dataStartup.profile.instagram"
          label="Instagram"
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field>
        <v-text-field
          v-model="dataStartup.profile.facebook"
          label="Facebook"
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field> 
        <v-text-field
          v-model="dataStartup.profile.youtube"
          label="Youtube"
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field>  
        <v-text-field
          v-model="dataStartup.profile.since"
          label="Since (yyyy-mm-dd)"
          dense
          :rules="dateRule"
          autocomplete="off"
        ></v-text-field> 
        <v-text-field
          v-model="dataStartup.profile.users"
          label="Total User"
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field> 
        <v-text-field
          v-model="dataStartup.profile.employe"
          label="Total Employee"
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field> 
        <img 
          v-if="dataStartup.profile.logo !== null"
          class="mt-3" 
          :src="`http://progresio.id/api/public/uploads/logo/${dataStartup.profile.logo}`" 
          alt="logo Startup"
          width="200" /> 
        <v-avatar 
          v-else
          size="80"
          color="grey lighten-3">
          Logo
        </v-avatar>
        <v-file-input
          accept="image/*"
          show-size
          counter
          label="Logo"
          v-model="logo"
        ></v-file-input> 
        <v-textarea
          v-model="dataStartup.profile.about"
          label="About"
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-textarea>  
        <div class="title mb-3">Profil Manager</div>                 
        <v-text-field
          v-model="dataStartup.profile.manager_name"
          label="Name"
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field> 
        <img 
          v-if="dataStartup.profile.manager_profile_foto !== null"
          class="mt-3" 
          :src="`http://progresio.id/api/public/uploads/profile/${dataStartup.profile.manager_profile_foto}`"
          alt="photo manager"
          width="200" /> 
        <v-avatar 
          v-else
          size="80"
          color="grey lighten-3">
          Photo
        </v-avatar>           
        <v-file-input
          accept="image/*"
          show-size
          counter
          label="Photo"
          v-model="photo"
        ></v-file-input>

        <Notif :msg="msg" :status="status" @visible="visible" />
			</v-card-text>
			<v-card-actions>
				<v-spacer></v-spacer>
				<v-btn
          color="primary"
          text
          @click="$router.go(-1)">
          kembali
        </v-btn>
        <v-btn
          :disabled="!valid"
          :loading="isLoading"
          color="primary"
          @click.prevent="validate"
          type="submit"
          depressed>
          simpan
        </v-btn>
			</v-card-actions>
		</v-form>
	</v-card>
</template>

<script>
  import { mapState } from 'vuex'
  import Notif from '@/components/Notif'

	export default{
		data: () => ({
			valid: true,
      requiredRule: [v => !!v || 'Data harus diisi'],
      photo: null,
      logo: null,
      msg: {
        success: 'Berhasil diubah',
        error: 'Gagal diubah',
        visible: false
      }
    }),

    components: {
      Notif
    },

		computed: {
      ...mapState('dataStartupStartup', ['dataStartup', 'isLoading', 'status']),
      dateRule () {
        return [
          ...this.requiredRule,
          v => !!v.match(/^(19[5-9][0-9]|20[0-4][0-9]|2050)$/igm) || 'Harus berupa tahun'
        ]
      }
    },

		methods: {
      validate () {
        if (this.$refs.form.validate()) {
          let data = new FormData()
            data.append('name', this.dataStartup.name)
            data.append('lokasi', this.dataStartup.profile.lokasi)
            data.append('phone', this.dataStartup.profile.phone)
            data.append('website', this.dataStartup.profile.website)
            data.append('instagram', this.dataStartup.profile.instagram)
            data.append('facebook', this.dataStartup.profile.facebook)
            data.append('youtube', this.dataStartup.profile.youtube)
            data.append('since', this.dataStartup.profile.since)
            data.append('about', this.dataStartup.profile.about)
            data.append('users', this.dataStartup.profile.users)
            data.append('employe', this.dataStartup.profile.employe)
            data.append('logo', this.logo)
            data.append('manager_name', this.dataStartup.profile.manager_name)
            data.append('manager_profile_foto', this.photo)
            data.append('email', this.dataStartup.email)
          // console.log(data)             
          this.$store.dispatch('dataStartupStartup/postDataStartup', data)
          this.$store.dispatch('dataStartupStartup/updateIsLoading', true)
          this.msg.visible = true
        }
      },
      visible(val) {
        this.msg.visible = val
        this.$store.commit('dataStartupStartup/updateStatus', null)
      }
    }
	}
</script>